<template>
  <form>
    <loading :loading="loading"></loading>
    <div class="row justify-content-center">
      <div class="col-lg-8">

        <!--DATE-->
        <div class="row">
          <div class="form-group col-md-6">
            <label for="date">Date</label>
            <datepicker id="date"></datepicker>
          </div>
        </div>

        <!--BASE ASSET-->
        <div class="row">
          <div class="form-group col-md-6">
            <label for="asset">Base asset</label>
            <multiselect
              id="asset"
              v-model="baseAsset"
              :options="baseAssets"
              label="format" track-by="name"
              @select="onSelectBaseAsset"
              :allow-empty="false"
              deselect-label="" select-label=""
            ></multiselect>
            <div class="invalid-feedback">Please, select source currency.</div>
          </div>

          <!--ASSETS 2-->
          <div class="form-group col-md-6">
            <label for="assets">Assets 2</label>
            <multiselect
              id="assets"
              v-model="pricesSelect"
              :options="pricesSelectArr"
              label="format" track-by="id"
              :multiple="true"
              @select="onSelectPrice"
              @remove="onRemovePrice"
              select-label=""
              :disabled="!baseAsset"
            ></multiselect>
            <div class="invalid-feedback">Please, select target assets.</div>
          </div>
        </div>

        <!--CLIENTS-->
        <div class="row">
          <div class="form-group col-md-6">
            <label for="clients">Clients</label>
            <multiselect
              id="clients"
              v-model="clientsSelect"
              :options="clientsSelectArr"
              :multiple="true"
              label="fullName" track-by="id"
              @select="onSelectClient"
              @remove="onRemoveClient"
              select-label=""
              :disabled="!baseAsset || !pricesSelect"
            ></multiselect>
            <div class="invalid-feedback">Please, select clients.</div>
          </div>
        </div>
      </div>
    </div>

    <!--TABLE-->
    <div class="row mt-5" v-if="tableValues[0].isSelected">
      <h1 class="h2">Input transactions</h1>
      <table class="table text-center">
        <thead>
          <tr>
            <th scope="col">Asset</th>
            <th scope="col">Before</th>
            <th scope="col">CCY1</th>
            <th scope="col">CCY2</th>
            <th scope="col">Exchange fee</th>
            <th scope="col">FX</th>
            <th scope="col">B Fee</th>
            <th scope="col">After</th>
            <th scope="col">Remark</th>
            <th scope="col">Check</th>
          </tr>
        </thead>
        <tbody>
          <!--ROWS-->
          <tr v-for="(values, index) in tableValues" :key="values.baseAsset">
            <!--FIRST-->
            <template v-if="!index">
              <th scope="row">{{ values.baseAsset }}</th>
              <td>{{ values.before.toFixed(8) }}</td>
              <td v-bind:class="{ 'table-warning': values.checkLTE }">{{ values.ccy1.toFixed(8) }}</td>
              <td>N/A</td>
              <td>0</td>
              <td>1</td>
              <td>
                <div class="input-group">
                  <div class="input-group-addon">
                    <span class="input-group-text">{{ values.baseAsset }}</span>
                  </div>
                  <input type="text" class="form-control text-right w-100" v-model.number="values.blockchainFee" @input="inputBFee" @keydown="inputBFee" @blur="inputBFee">
                </div>
              </td>
              <td>
                <div class="input-group">
                  <div class="input-group-addon">
                    <span class="input-group-text">{{ values.baseAsset }}</span>
                  </div>
                  <input type="text" class="form-control text-right w-100" v-model.number="values.after" @input="inputBFee" @keydown="inputBFee" @blur="inputBFee">
                </div>
              </td>
              <td>
                <textarea class="form-control" rows="2" v-model="values.remark"></textarea>
              </td>
              <td>
                <span v-if="values.check" style="color: transparent; text-shadow: 0 0 0 green;">&#10004;</span>
                <span v-if="!values.check" style="color: transparent; text-shadow: 0 0 0 red;">&#10006;</span>
              </td>
            </template>
            <!--REMAINING-->
            <template v-else>
              <th scope="row">{{ values.baseAsset }}</th>
              <td>{{ values.before.toFixed(8) }}</td>
              <td>
                <div class="input-group">
                  <div class="input-group-addon">
                    <span class="input-group-text">{{ values.baseAsset }}</span>
                  </div>
                  <input type="text" class="form-control text-right w-100" v-model.number="values.ccy1" @input="inputBFee" @keydown="inputBFee" @blur="inputBFee">
                </div>
              </td>
              <td>
                <div class="input-group">
                  <div class="input-group-addon">
                    <span class="input-group-text">{{ tableValues[0].baseAsset }}</span>
                  </div>
                  <input type="text" class="form-control text-right w-100" v-model.number="values.ccy2" @input="inputBFee" @keydown="inputBFee" @blur="inputBFee">
                </div>
              </td>
              <td>
                <div class="input-group">
                  <multiselect
                    v-model="values.exchangeFeeValue"
                    :options="values.exchangeFeeOptions"
                    :allow-empty="false"
                    :show-labels="false"
                    :searchable="false"
                    @input="inputBFee"
                  ></multiselect>
                  <input type="text" class="form-control text-right w-100" v-model.number="values.exchangeFee" @input="inputBFee" @keydown="inputBFee" @blur="inputBFee">
                </div>
              </td>
              <td>
                {{ parseFloat(values.fx).toFixed(8) }}
              </td>
              <td>
                <div class="input-group">
                  <div class="input-group-addon">
                    <span class="input-group-text">{{ values.baseAsset }}</span>
                  </div>
                  <input type="text" class="form-control text-right w-100" v-model.number="values.blockchainFee" @input="inputBFee" @keydown="inputBFee" @blur="inputBFee">
                </div>
              </td>
              <td>
                <div class="input-group">
                  <div class="input-group-addon">
                    <span class="input-group-text">{{ values.baseAsset }}</span>
                  </div>
                  <input type="text" class="form-control text-right w-100" v-model.number="values.after" @input="inputBFee" @keydown="inputBFee" @blur="inputBFee">
                </div>
              </td>
              <td>{{ values.remark }}</td>
              <td>
                <span v-if="values.check" style="color: transparent; text-shadow: 0 0 0 green;">&#10004;</span>
                <span v-if="!values.check" style="color: transparent; text-shadow: 0 0 0 red;">&#10006;</span>
              </td>
            </template>
          </tr>
        </tbody>
      </table>
    </div>

    <!--TABLES-->
    <div class="list-group-item row mt-5" v-for="(values, index) in getClients">
      <div class="row pt-3">
        <div class="col d-flex justify-content-start">{{ values.fullName }}</div>
        <div class="col d-flex justify-content-end">
          {{ parseFloat(values.details.baseAssetPieces).toFixed(8) }} {{ values.details.assets[baseAsset.ticker].ticker }}
        </div>
        <div class="col d-flex justify-content-end">
          {{ parseFloat(values.details.percentage * 100).toFixed(4) }}%
        </div>
      </div>
      <table class="table text-center table-striped">
        <thead>
          <tr>
            <th scope="col">Asset</th>
            <th scope="col">Before</th>
            <th scope="col">CCY1 Amount</th>
            <th scope="col">CCY2 Amount</th>
            <th scope="col">FX</th>
            <th scope="col">B Fee</th>
            <th scope="col">After</th>
            <th scope="col">Check</th>
          </tr>
        </thead>
        <tbody>
          <!--FIRST ROW-->
          <tr>
            <th scope="row">{{ tableValues[0].baseAsset }}</th>
            <td>{{ parseFloat(values.details.baseAssetPieces).toFixed(8) || '' }}</td>
            <td>N/A</td>
            <td>
              <span v-if="values.details.ccy2" style="color: transparent; text-shadow: 0 0 0 green;">&#10004;</span>
              <span v-if="!values.details.ccy2" style="color: transparent; text-shadow: 0 0 0 red;">&#10006;</span>
            </td>
            <td>1</td>
            <td>{{ parseFloat(values.details.blockchainFee).toFixed(8) || '' }}</td>
            <td>{{ parseFloat(values.details.after).toFixed(8) || '' }}</td>
            <td></td>
          </tr>
          <!--ROWS-->
          <tr v-for="(prices, i) in getPrices">
            <th scope="row">{{ prices.symbol }}</th>
            <td>
              {{ parseFloat(values.details.pricesPieces[i]).toFixed(8) || '' }}
            </td>
            <td>
              {{ parseFloat(values.details.ccy1Arr[i]).toFixed(8) || '' }}
            </td>
            <td>
              {{ parseFloat(values.details.ccy2Arr[i]).toFixed(8) || '' }}
            </td>
            <td>
              {{ parseFloat(values.details.fxArr[i]).toFixed(8) || '' }}
            </td>
            <td>
              {{ parseFloat(values.details.blockchainFees[i]).toFixed(8) || '' }}
            </td>
            <td>
              {{ parseFloat(values.details.afters[i]).toFixed(8) || '' }}
            </td>
            <td></td>
          </tr>
        </tbody>
      </table>
    </div>
<!--    {{uploads}}-->

    <div class="row mt-5 justify-content-center">
      <div class="form-group">
        <button type="submit" disabled="disabled" class="btn btn-lg btn-block btn-outline-success">Submit transactions</button>
      </div>
    </div>
  </form>
</template>


<script>
  import axios from 'axios'
  import Datepicker from 'vuejs-datepicker'
  import Multiselect from 'vue-multiselect'
  import Decimal from 'decimal.js'

  // Big.RM = 0
  const API_V1_URL = process.env.API_V1_URL
  Decimal.set({ rounding: 5 })

  export default {
    components: {
      Datepicker,
      Multiselect,
    },
    data() {
      return {
        loading: false,
        // Objects to keep in memory
        assets: {},
        clients: {},
        currencies: {},
        // Select value
        baseAsset: null,
        baseAssets: [],
        pricesSelect: null,
        pricesSelectArr: [],
        clientsSelect: null,
        clientsSelectArr: [],
        // Table values
        tableValues: [{
          isSelected: false,
          checkLTE: false,
          baseAsset: '',
          before: 0,
          ccy1: 0,
          ccy2: 0,
          exchangeFee: 0,
          fx: 0,
          blockchainFee: 0,
          after: 0,
          remark: '',
          check: '',
        }],
        computedClients: [],
      }
    },
    computed: {
      getClients() {
        /**
         * Calculate everything with a cloned object in order to not get render loops!
         */
        if (this.clientsSelect) {
          var funds = this.tableValues
          // Deep clone of the selected clients
          this.computedClients = JSON.parse(JSON.stringify(this.clientsSelect))
          var allClientsCCY2 = new Decimal(0)
          var allClientsAssetsSums = []
          var totalBase = new Decimal(0)
          var totalBaseArr = []
          this.computedClients.forEach((el, index) => {
            var totalInBase = new Decimal(0)
            var client = el.details
            // BEFORE
            client.baseAssetPieces = new Decimal(client.assets[this.baseAsset.ticker].pieces)
            // PERCENTAGE = BEFORE / FUND[BEFORE]
            JSON.parse(JSON.stringify(this.pricesSelect)).forEach((prices, i) => {
              // BEFORE
              client.pricesPieces[i] = client.assets[prices.symbol]
                ? new Decimal(client.assets[prices.symbol].pieces)
                : new Decimal(0)
              // CALCULATE TOTAL FX
              funds[i + 1].fx = parseFloat(funds[i + 1].ccy2) ? new Decimal(funds[i + 1].ccy2).times(-1).div(parseFloat(new Decimal(funds[i + 1].ccy1).plus(parseFloat(funds[i + 1].exchangeFee)))) : 0
              // CALCULATE TOTAL IN BASE ASSET
              totalInBase = totalInBase.plus(client.pricesPieces[i].times(funds[i + 1].fx))
            })
            totalBaseArr[index] = totalInBase.plus(client.assets[this.baseAsset.ticker].pieces)
            totalBase = totalBase.plus(totalInBase).plus(client.assets[this.baseAsset.ticker].pieces)
          })
          /**
           * LOOP THROUGH CLIENTS
           * the code before the assets loop is only for the first row
           */
          this.computedClients.forEach((el, index) => {
            var client = el.details
            // BEFORE
            client.baseAssetPieces = new Decimal(client.assets[this.baseAsset.ticker].pieces)
            client.percentage = parseFloat(totalBaseArr[index])
              ? totalBaseArr[index].div(totalBase)
              : new Decimal(0)
            // BFee = FUND[BFee] * PERCENTAGE
            client.blockchainFee = new Decimal(funds[0].blockchainFee || 0).times(parseFloat(client.percentage))
            // AFTER = FUND[AFTER] * PERCENTAGE
            client.after = new Decimal(funds[0].after || 0).times(parseFloat(client.percentage))
            // count CCY2 for the check => BEFORE + BFee ( + all clients' CCY2)
            allClientsCCY2 = allClientsCCY2.plus(parseFloat(client.baseAssetPieces)).plus(parseFloat(client.blockchainFee))
            /**
             * LOOP THROUGH ASSETS to generate the next rows
             */
            JSON.parse(JSON.stringify(this.pricesSelect)).forEach((prices, i) => {
              // BEFORE
              client.pricesPieces[i] = client.assets[prices.symbol]
                ? new Decimal(client.assets[prices.symbol].pieces)
                : new Decimal(0)
              // BFee = FUND[BF] * PERCENTAGE
              client.blockchainFees[i] = new Decimal(funds[i + 1].blockchainFee || 0).times(parseFloat(client.percentage))
              // AFTER = FUND[AFTER] * PERCENTAGE
              client.afters[i] = new Decimal(funds[i + 1].after || 0).times(parseFloat(client.percentage))
              // CCY1 = AFTER - BEFORE + BFee
              client.ccy1Arr[i] = client.afters[i].minus(client.pricesPieces[i]).minus(client.blockchainFees[i])
              // CCY2 = FUND[FX] * -CCY1
              client.ccy2Arr[i] = new Decimal(parseFloat(funds[i + 1].fx)).times(parseFloat(client.ccy1Arr[i] || 0)).times(-1)
              // FX = CCY2 / CCY1
              client.fxArr[i] = Math.abs(parseFloat(parseFloat(client.ccy2Arr[i])
                ? client.ccy2Arr[i].div(parseFloat(client.ccy1Arr[i]))
                : new Decimal(0)
              ))
              // Add + all clients' CCY2 to the check
              allClientsCCY2 = allClientsCCY2.plus(parseFloat(client.ccy2Arr[i]))
              // FUND CHECK (summed for all clients) = BEFORE + CCY1 + BFee
              allClientsAssetsSums[i] = client.pricesPieces[i]
                .plus(parseFloat(client.ccy1Arr[i]))
                .plus(parseFloat(client.blockchainFees[i])).plus(parseFloat(allClientsAssetsSums[i] || 0))
            })
            // CHECK CCY2 IN THE FIRST ROW OF A CLIENT'S TABLE
            var ccy2 = new Decimal(0)
            client.ccy2Arr.forEach(el => {
              ccy2 = ccy2.plus(parseFloat(el))
            })
            if (client.ccy2Arr.length) {
              client.ccy2 = new Decimal(client.baseAssetPieces.plus(parseFloat(client.blockchainFee)).plus(ccy2)).toDP(8).eq(client.after.toDP(8))
            }
          })
          // CHECK FUNDS[0].Before + FUNDS[1+].CCY2 <= FUNDS[0].Before
          funds[0].checkLTE = new Decimal(funds[0].before).plus(funds[0].blockchainFee).lte(Math.abs(funds[0].ccy1))
          // CHECK FUNDS[0].AFTER === CLIENTS[CCY2]
          funds[0].check = allClientsCCY2.toDP(8).eq(parseFloat(new Decimal(funds[0].after).toDP(8)))
          // CHECK FUNDS[1+].AFTER === allClientsAssetsSums
          JSON.parse(JSON.stringify(this.pricesSelect)).forEach((prices, i) => {
            funds[i + 1].check = allClientsAssetsSums[i].toDP(8).eq(parseFloat(new Decimal(funds[i + 1].after).toDP(8)))
          })
          return this.computedClients
        }
      },
      getPrices() {
        return JSON.parse(JSON.stringify(this.pricesSelect))
      },
      //   transactions() {
      //     return this.$store.getters.getSPTransactions
      //   },
      //   total() {
      //     return this.$store.getters.getSPTransactionsTotal
      //   },
      //   uploads() {
      //     return this.$store.getters.getRecentUploadsSummary
      //   },
    },
    created() {
      // this.loadRecentUploads()
      // axios.get(API_V1_URL + '/portfolio/0/tx')
      //   .then((res) => {
      //     console.log(res)
      //   })
      /**
       * Start loading
       */
      this.loading = true
      this.$store.dispatch('loadTotalPortfolios')
      /**
       * Load Portfolios
       */
        .then((data) => {
          var assets = this.$store.getters.getTPAssets
          assets.forEach((el) => {
            el.format = el.name + ' - ' + parseFloat(el.pieces).toFixed(8)
            this.currencies[el.name] = el
          })
          this.baseAssets = assets
        })
        /**
         * Load Prices
         */
        .then(() => {
          axios.get(API_V1_URL + '/prices/?limit=1000&offset=0')
            .then((res) => {
              var prices = []
              var fullPrices = []
              res.data.forEach((el) => {
                if (this.currencies[el.name]) {
                  el.format = el.id + ' [' + el.symbol + ']' + ' - ' + parseFloat(this.currencies[el.name].pieces).toFixed(8)
                  fullPrices.push(el)
                } else {
                  el.format = el.id + ' [' + el.symbol + ']'
                  prices.push(el)
                }
              })
              this.pricesSelectArr = fullPrices.concat(prices)
            })
        })
        /**
         * Load Clients
         */
        .then(() => {
          axios.get(API_V1_URL + '/users?role=client&offset=0&limit=1000')
            .then((res) => {
              res.data.forEach((el) => {
                el.fullName = el.firstName + ' ' + el.lastName + ' [' + el.clientId + ']'
                el.details = {
                  assets: {},
                  baseAssetPieces: 0,
                  pricesPieces: [],
                  percentage: 0,
                  blockchainFees: [],
                  afters: [],
                  ccy1Arr: [],
                  ccy2Arr: [],
                  fxArr: [],
                }
                this.clients[el.id] = el
              })
            })
        })
        /**
         * Stop loading
         */
        .finally(() => {
          this.loading = false
        })
    },
    methods: {
      /**
       * Load Clients who have the currency in their assets
       */
      onSelectBaseAsset(value) {
        this.loading = true
        // Prepare table
        this.tableValues[0].isSelected = false
        this.tableValues[0].baseAsset = this.currencies[value.name].ticker
        // Make sure the user can't select the same prices
        var found = this.pricesSelectArr.find(function(el) {
          return el['$isDisabled'] === true
        })
        if (found) {
          found['$isDisabled'] = false
        }
        var found2 = this.pricesSelectArr.find(function(el) {
          return value.ticker === el.symbol
        })
        if (found2) {
          found2['$isDisabled'] = true
        }
        // Reset values
        if (this.clientsSelect && this.clientsSelect.length) {
          for (let values of this.tableValues) {
            values.before = 0
          }
          this.clientsSelect.forEach((el) => {
            this.onSelectClient(el)
          })
        }
        if (this.pricesSelect) {
          this.tableValues.forEach(function(el, i) {
            if (i) {
              el.price = 0
            }
          })
          for (let [key, price] of Object.entries(this.pricesSelect)) {
            if (key !== this.tableValues[0].symbol) {
              this.onSelectPrice(price, 0, true)
            }
          }
        }
        // Generate requests
        var requests = Object.keys(this.clients).map((id) => {
          var ids = []
          this.clients[id].portfolios.forEach(function(portfolio) {
            ids.push(axios.get(API_V1_URL + '/portfolio/' + portfolio.id))
          })
          return ids
        })
        // Search in all assets
        var reqs = [].concat(...requests)
        axios.all(reqs)
          .then((responses) => {
            var clients = []
            responses.forEach((res) => {
              this.assets[res.data.userId] = res.data.assets
              for (let asset of res.data.assets) {
                if (asset.name === value.name) {
                  var client = this.clients[res.data.userId]
                  clients.push(client)
                }
              }
            })
            clients.sort((a, b) => a.fullName.localeCompare(b.fullName))
            this.clientsSelectArr = clients
            this.loading = false
            // Show the table
            this.tableValues[0].isSelected = true
          })
      },
      /**
       * Client select methods
       */
      onSelectClient(value) {
        for (let portfolio of this.assets[value.id]) {
          this.tableValues.forEach((el) => {
            if (portfolio.ticker === el.baseAsset) {
              el.before = parseFloat(new Decimal(portfolio.pieces).plus(el.before))
              value.details['assets'][el.baseAsset] = portfolio
            }
          })
        }
        this.inputBFee()
      },
      onRemoveClient(value) {
        for (let portfolio of this.assets[value.id]) {
          this.tableValues.forEach(function(el) {
            if (portfolio.ticker === el.baseAsset) {
              el.before = parseFloat(new Decimal(el.before).minus(portfolio.pieces))
              delete value.details['assets'][el.baseAsset]
            }
          })
        }
        this.inputBFee()
      },
      /**
       * Price select methods
       */
      onSelectPrice(value, id, hasBaseAssetChanged) {
        // Make sure the user can't select the same assets
        Object.keys(this.currencies).forEach((key) => {
          if (value.symbol === this.currencies[key].ticker) {
            this.currencies[key]['$isDisabled'] = !this.currencies[key]['$isDisabled']
          }
        })
        var options = [value.symbol, this.tableValues[0].baseAsset]
        // Create new row
        if (!hasBaseAssetChanged) {
          this.tableValues.push({
            baseAsset: value.symbol,
            before: 0,
            ccy1: 0,
            ccy2: 0,
            exchangeFee: 0,
            fx: 0,
            blockchainFee: 0,
            after: 0,
            remark: '',
            check: '',
            exchangeFeeValue: options[0],
            exchangeFeeOptions: options,
          })
        }
        /**
         * Calculate "before" field for each client
         */
        if (this.clientsSelect) {
          for (let [, client] of Object.entries(this.clientsSelect)) {
            for (let portfolio of client.portfolios) {
              var found = this.assets[portfolio.userId].find(function(el) {
                return el.ticker === value.symbol
              })
              if (found) {
                for (let row of this.tableValues) {
                  if (row.baseAsset === value.symbol) {
                    row.before = parseFloat(new Decimal(found.pieces).plus(row.before))
                    client.details['assets'][value.symbol] = found
                  }
                }
              }
            }
          }
        }
      },
      onRemovePrice(value) {
        // Make sure the user can't select the same assets
        Object.keys(this.currencies).forEach((key) => {
          if (value.symbol === this.currencies[key].ticker) {
            this.currencies[key]['$isDisabled'] = !this.currencies[key]['$isDisabled']
          }
        })
        // Remove row from fund table
        this.tableValues.splice(this.tableValues.findIndex(val => {
          return val.baseAsset === value.symbol
        }), 1)
        this.inputBFee()
      },
      /**
       * Calculations
       */
      inputBFee(e) {
        if (e && e.target) {
          if (!RegExp('^-?[0-9]+([\\.][0-9]+)?$').test(e.target.value)) {
            e.target.value = e.target.value.replace(',', '.')
            e.target.closest('td').classList.add('table-danger')
          } else {
            e.target.closest('td').classList.remove('table-danger')
          }
        }
        this.inputCCY2()
        this.inputCCY1()
      },
      inputCCY1(e) {
        var sumCCY2 = 0
        this.tableValues.forEach((el, i) => {
          if (i) {
            sumCCY2 = new Decimal(parseFloat(sumCCY2)).plus(el.ccy2)
          }
        })
        this.tableValues[0].after = parseFloat(new Decimal(this.tableValues[0].before || 0).plus(parseFloat(sumCCY2) || 0).plus(this.tableValues[0].blockchainFee || 0))
        this.tableValues.forEach((el, i) => {
          if (i) {
            sumCCY2 = new Decimal(parseFloat(sumCCY2)).plus(el.ccy2)
            this.tableValues[i].after = parseFloat(new Decimal(el.before).plus(el.ccy1).plus(el.blockchainFee)) || 0
            if (this.tableValues[i].exchangeFeeValue !== this.tableValues[0].baseAsset) {
              // FUND[i].AFTER = Before + CCY1 + ExchangeFee
              this.tableValues[i].after = parseFloat(new Decimal(this.tableValues[i].after).plus(el.exchangeFee || 0))
            } else {
              // FUND[0].AFTER += FUND[i].ExchangeFee
              this.tableValues[0].after = parseFloat(new Decimal(this.tableValues[0].after).plus(this.tableValues[i].exchangeFee))
            }
          }
        })
      },
      inputCCY2(e) {
        this.tableValues[0].ccy1 = 0
        this.tableValues.forEach((el, i) => {
          if (i) {
            this.tableValues[0].ccy1 = parseFloat(new Decimal(this.tableValues[0].ccy1 || 0).plus(el.ccy2 || 0))
          }
        })
      },
      // loadRecentUploads() {
      //   var self = this
      //   this.$store.dispatch('loadRecentUploadsSummary')
      //     .then(uploads => {
      //       window.clearTimeout(self.timeoutID)
      //       const hasUnprocessedFile = uploads.find(upload => upload.status === 'pending' || upload.status === 'working')
      //       if (hasUnprocessedFile) {
      //         self.timeoutID = window.setTimeout(self.loadRecentUploads, 1000)
      //       }
      //     })
      // },
    },
  }
</script>

